export default function BookUpsell({ isOpen, handleClose }: any) {
  const t = useTranslations();
  const intl = useIntl();
  const { searchState, currency } = useGlobal();
  const { locale } = useRouter();

  const hotelsUrl = useMemo(() => {
    const checkIn =
      searchState?.departureDate &&
      new Date(searchState.departureDate).toISOString().split("T")[0];
    const checkOut = searchState?.returnDate
      ? new Date(searchState.returnDate).toISOString().split("T")[0]
      : new Date(new Date(checkIn).setDate(new Date(checkIn).getDate() + 2));
    return `https://tp.media/r?marker=56123&trs=15329&p=2076&u=https%3A%2F%2Fwww.booking.com%2Fsearchresults.en-gb.html%3Fss%3D${searchState?.to?.city_name}%2C+${searchState?.to?.country_name}%26checkin%3D${checkIn}%26checkout%3D${checkOut}`;
  }, [searchState?.departureDate, searchState?.returnDate, searchState?.to]);

  const carsUrl = useMemo(() => {
    const checkIn =
      searchState?.departureDate &&
      new Date(searchState.departureDate).toISOString().split("T")[0];
    const checkOut = searchState?.returnDate
      ? new Date(searchState.returnDate).toISOString().split("T")[0]
      : new Date(new Date(checkIn).setDate(new Date(checkIn).getDate() + 2));
    return `https://book.cartrawler.com/?clientID=750756&pickupDateTime=${checkIn}T12%3A00&returnDateTime=${checkOut}T12%3A00&age=30&ct=MP&curr=${currency.code}&ln=${locale}&pickupName=${searchState?.to?.city_name}%2C+${searchState?.to?.country_name}&returnName=${searchState?.to?.city_name}%2C+${searchState?.to?.country_name}#/vehicles`;
  }, [
    searchState?.departureDate,
    searchState?.returnDate,
    searchState?.to,
    currency,
    locale,
  ]);

  const handleHotels = useCallback(() => {
    window.open(hotelsUrl);
  }, [hotelsUrl]);

  const handleCars = useCallback(() => {
    window.open(carsUrl);
}, [carsUrl]);


  return (
    <Dialog onClose={handleClose} open={isOpen}>
      <DialogTitle>
        {t("book-now-upsell-title", { value: searchState?.to?.city_name })}
      </DialogTitle>
      <DialogContent>
        <Typography sx={{ mb: 1 }}>{t("book-now-upsell-sub-title")}</Typography>
        <Typography sx={{ mb: 3, textAlign: 'center' }}>
          <span>
            <strong>
              {intl.formatDateTime(searchState?.departureDate, {
                month: "long",
                day: "numeric",
                weekday: "short",
              })}
            </strong>
          </span>
          {searchState?.returnDate && (
            <span>
              <strong>
                {" - "}
                {intl.formatDateTime(searchState?.returnDate, {
                  month: "long",
                  day: "numeric",
                  weekday: "short",
                })}
              </strong>
            </span>
          )}
        </Typography>
        <Box sx={{ display: "flex", flexDirection: "column" }}>
          <Button
            sx={{ my: 1 }}
            variant="contained"
            startIcon={<HotelIcon />}
            endIcon={<OpenInNewIcon />}
            onClick={handleHotels}
          >
            {t("book-now-upsell-search-hotels")} (Booking.com)
          </Button>
          <Button
            sx={{ mb: 1 }}
            variant="contained"
            startIcon={<CarRentalIcon />}
            endIcon={<OpenInNewIcon />}
            onClick={handleCars}
          >
            {t("book-now-upsell-search-cars")}
          </Button>
        </Box>
        {/* <Typography sx={{ textAlign: "center" }}>
          {t("book-now-upsell-opens-in-tab")}
        </Typography> */}
      </DialogContent>

      {/* 
            <div style="margin-bottom: 1.5rem;">
                <p *ngIf="toDate"><strong>{{fromDate | date:'EEEE, d MMMM':'UTC':locale}} - {{toDate | date:'EEEE, d MMMM':'UTC':locale}}</strong></p>
                <p *ngIf="!toDate"><strong>{{fromDate | date:'EEEE, d MMMM':'UTC':locale}}</strong></p>
            </div>
            <a style="margin-bottom: 0.5rem; width: 100%;" mat-stroked-button color="primary" [href]="getHotelsUrl()" target="_blank" (click)="clickedHotels()">
                <mat-icon>hotel</mat-icon>
                {{'book-now-upsell-search-hotels' | transloco}}
                <!-- <mat-icon>open_in_new</mat-icon> -->
            </a>
            <a style="width: 100%;" mat-stroked-button color="primary" [href]="getCarsUrl()" target="_blank" (click)="clickedCars()">
                <mat-icon>directions_car</mat-icon>
                {{'book-now-upsell-search-cars' | transloco}}
                <!-- <mat-icon>open_in_new</mat-icon> -->
            </a>
*/}
    </Dialog>
  );
}
